.profile__occupation {
    grid-area: job;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    padding: 0;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 300px;
}

@media screen and (max-width: 425px) {
    .profile__occupation {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }
}